@charset "UTF-8";
body {
  font-family: "ヒラギノ明朝 ProN";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.about {
  background-color: #F0F0EC;
  padding-bottom: 3.125rem;
}

.about__inner {
  padding-top: 1.3125rem;
}

.about__heading {
  margin-bottom: 0.625rem;
}

.about__content {
  display: flex;
  align-items: center;
  margin-bottom: 2.125rem;
}

.about__img {
  width: 6.125rem;
  height: 6rem;
}
.about__img img {
  border-radius: 50%;
}

.about__lead {
  margin-left: 1.5625rem;
  font-size: 0.8125rem;
}

.about__text {
  font-size: 0.75rem;
  line-height: 1.6666666667;
}